import { ref, computed, readonly } from 'vue'
import { getAllGames } from '@/data/gamesData'

// 
const searchQuery = ref('')
const activeCategory = ref('all')

// 
export const useSearch = () => {
    // 
    const setSearchQuery = (query) => {
        searchQuery.value = query.toLowerCase().trim()
    }

    // 
    const setActiveCategory = (category) => {
        activeCategory.value = category
    }

    // 
    const clearSearch = () => {
        searchQuery.value = ''
        activeCategory.value = 'all'
    }

    // 
    const filteredGames = computed(() => {
        let games = getAllGames() //  orderId 

        // 
        if (activeCategory.value !== 'all') {
            games = games.filter(game => game.category === activeCategory.value)
        }

        // 
        if (searchQuery.value) {
            games = games.filter(game => {
                const query = searchQuery.value
                return (
                    game.name.toLowerCase().includes(query) ||
                    game.description.toLowerCase().includes(query) ||
                    game.tags.some(tag => tag.id.toLowerCase().includes(query))
                )
            })
        }

        return games
    })

    // 
    const searchSuggestions = computed(() => {
        if (!searchQuery.value || searchQuery.value.length < 2) {
            return []
        }

        const suggestions = []
        const query = searchQuery.value
        const allGames = getAllGames()

        // 
        allGames.forEach(game => {
            if (game.name.toLowerCase().includes(query)) {
                suggestions.push({
                    type: 'game',
                    text: game.name,
                    value: game.name,
                    game: game
                })
            }
        })

        // 
        const allTags = new Set()
        allGames.forEach(game => {
            game.tags.forEach(tag => {
                if (tag.id.toLowerCase().includes(query)) {
                    allTags.add(tag.id)
                }
            })
        })

        allTags.forEach(tagId => {
            suggestions.push({
                type: 'tag',
                text: `#${tagId}`,
                value: tagId
            })
        })

        return suggestions.slice(0, 5) // 
    })

    return {
        searchQuery: readonly(searchQuery),
        activeCategory: readonly(activeCategory),
        filteredGames,
        searchSuggestions,
        setSearchQuery,
        setActiveCategory,
        clearSearch
    }
}