</template>

<script>
import { ref } from 'vue'
import LanguageSelector from './LanguageSelector.vue'
import SearchBox from './SearchBox.vue'

export default {
  name: 'Header',
  components: {
    LanguageSelector,
    SearchBox
  },
  setup() {
    const mobileMenuOpen = ref(false)
    
    const toggleMobileMenu = () => {
      mobileMenuOpen.value = !mobileMenuOpen.value
    }
    
    return {
      mobileMenuOpen,
      toggleMobileMenu
    }
  }
}
</script>